import Joi from '@hapi/joi';

import { Workflow } from '../constants';

import { GenericAndroidJob, GenericAndroidJobSchema } from './generic/job';
import { ManagedAndroidJob, ManagedAndroidJobSchema } from './managed/job';

type AndroidJob = GenericAndroidJob | ManagedAndroidJob;

const AndroidJobSchema = Joi.object({
  type: Joi.string()
    .valid(...Object.values(Workflow))
    .required(),
})
  .when(Joi.object({ type: Workflow.Generic }).unknown(), { then: GenericAndroidJobSchema })
  .when(Joi.object({ type: Workflow.Managed }).unknown(), { then: ManagedAndroidJobSchema });

export { AndroidJob, AndroidJobSchema };
